@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

body {
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #53dfd1;
  color: #343a40;
}

.displayTable{width:50%;height:100%;position:relative}.event-heading{text-align:center;font-size:2rem;padding:10px;height:8%}.loader{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}@media(max-width: 991.98px){.displayTable{width:85%}.event-heading{font-size:1.8rem}}@media(max-width: 767.98px){.displayTable{width:95%}.event-heading{font-size:1.6rem}}@media(max-width: 575.98px){.displayTable{width:97%;height:90%}.event-heading{font-size:1.4rem}}
.leaderboard-table{width:100%;height:100%}.table-headers{height:10%;width:100%;display:flex;font-weight:700;text-decoration:underline;border-bottom:1px dotted #000}.table-content-ranking,.table-content-name,.table-content-startTime,.table-content-finishTime{height:100%;display:flex;align-items:center;justify-content:flex-start}.table-content-ranking{width:15%;justify-content:center}.table-content-name{width:40%}.table-content-startTime{width:25%}.table-content-finishTime{width:20%}
.winner-table{width:100%;height:36%;position:relative}.winner-loader{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.addWinner{height:calc(100% / 3) !important;background-color:#fff !important;font-size:.9rem !important}
.rest-table-body{width:100%;height:54%;overflow-y:scroll}.table-content{height:50px;display:flex;background-color:#e0ebe5;font-size:.75rem}.table-content:nth-of-type(2n){background-color:#b8ddd6}
.Player-Data{width:50%;height:100%;border-right:1px dotted #000}.Player-data-headers{height:10%;width:100%;display:flex;font-weight:700;text-decoration:underline;border-bottom:1px dotted #000}.Player-data-id,.Player-data-name,.Player-data-distance{height:100%;display:flex;align-items:center;justify-content:center}.Player-data-id{width:15%}.Player-data-name{width:50%;justify-content:flex-start}.Player-data-distance{width:35%}.Player-data-main{height:90%;width:100%;overflow-y:scroll;background-color:#e0ebe5}.Player-Data-Row{height:50px;display:flex;background-color:#e0ebe5}.Player-Data-Row:nth-of-type(2n){background-color:#b8ddd6}
.App-Component{width:100vw;height:90vh;display:flex;flex-direction:column}.App-heading{text-align:center;padding:25px}.App-Table-Heading{display:flex;margin:0 auto;width:74%;font-weight:700;text-transform:uppercase;justify-content:space-around;padding:10px}.Table-Content{width:75%;height:75%;margin:0 auto;display:flex;flex-direction:row;border-top:1px dotted #000;border-left:1px dotted #000;border-right:1px dotted #000}.Button-Start{height:10%;width:20%;margin:10px auto;display:flex;align-items:center;justify-content:center}.BTN-start-el{background:none;border:2px solid;padding:1em 3em;font-size:1em;transition:all .25s}.BTN-start-el:hover{border-color:#343a40;box-shadow:0 .7em .7em -0.5em;transform:translateY(-0.25em);cursor:pointer}.small-screen-message{display:none}@media screen and (max-width: 1200px){.small-screen-message{position:absolute;height:100vh;width:100%;background-color:#000;color:#fff;display:flex;justify-content:center;align-items:center;text-align:center;z-index:100}}
